/* Variables */
:root {
  /* Greys */
  --color-grey-1: #E8E8E8;
  --color-grey-2: #EFEFEF;
  --color-grey-3: #A9A9A9;
  --color-grey-4: #5F5F5F;
  /* General */
  --color-primary: var(--color-grey-1);
  --color-blue: #4589AD;
  --color-blue-light:#9BB6CF;
  --color-blue-dark: #356F8E;
  --color-black: #2B2B2B;
  --color-white: #FFFFFF;
  --color-red: #B90000;
  --color-trivia: #226182;
  --color-year:#176152;
  --color-math: #6F6C4D;
  /* Greens */
  --color-green-1: #85C385;
  --color-green-1-dark: #6fb86f;
  --color-green-2: #59AD59;
  --color-green-2-dark: #4B994B;
  /* Elements-related */
  --color-body-bg: var(--color-grey-1);
  /* Sizes */
  --size-1: .5rem;
  --size-2: calc(2 * var(--size-1));
  --size-3: calc(3 * var(--size-1));
  --size-4: calc(4 * var(--size-1));
  --size-5: calc(5 * var(--size-1));
  --size-6: calc(6 * var(--size-1));
  --root-font-size: 62.5%; /* 1rem = 10px */
  --app-margin-size: var(--size-6);
}

/* Global styles */
* {
  box-sizing: border-box;
}

body,
p,
h1,
h2 {
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
  font-size: var(--root-font-size);
}

body {
  background: #E8E8E8;
  background: var(--color-body-bg);
  font-family: 'Roboto';
  font-size: 1.6rem;
}
.header {
  margin-bottom: var(--size-4);
}

.header-title {
  font-family: 'Ubuntu', sans-serif;
  font-size: 4rem;
  font-weight: 700;
  text-align: center;
}

.header-title__num {
  color: var(--color-trivia);
}

.header-title__in {
  color: var(--color-year);
}

.header-title__fo {
  color: var(--color-math);
}
.footer {
  font-size: 1.4rem;
  margin-top: var(--size-4);
  text-align: right;
}

.footer-link {
  color: var(--color-blue);
  text-decoration: none;
}

.footer-link:hover {
  text-decoration: underline;
}

.footer-author-info {
  margin-top: var(--size-1);
}

.footer-by {
  color: var(--color-grey-4);
  font-size: 1rem;
}

.footer-separator {
  color: var(--color-grey-3);
  margin: 0 1rem;
}
.main-content-search-form-input {
  background-color: var(--color-white);
  border: 0;
  color: var(--color-black);
  font-family: 'Roboto';
  font-size: 2.8rem;
  padding: var(--size-3) var(--size-4);
  width: 100%;
}

.main-content-search-form-input:focus {
  outline: none;
}

.main-content-search-form-input::-webkit-input-placeholder {
  color: var(--color-grey-3);
  font-family: 'Ubuntu', sans-serif;
}

.main-content-search-form-input::-ms-input-placeholder {
  color: var(--color-grey-3);
  font-family: 'Ubuntu', sans-serif;
}

.main-content-search-form-input::placeholder {
  color: var(--color-grey-3);
  font-family: 'Ubuntu', sans-serif;
}
@-webkit-keyframes mainContentSearchFormButton {
  0% {
    background-color: var(--color-green-1);
    border-color: var(--color-green-1-dark);
  }
  
  50% {
    background-color: var(--color-green-2);
    border-color: var(--color-green-2-dark);
  }

  100% {
    background-color: var(--color-green-1);
    border-color: var(--color-green-1-dark);
  }
}

@keyframes mainContentSearchFormButton {
  0% {
    background-color: var(--color-green-1);
    border-color: var(--color-green-1-dark);
  }
  
  50% {
    background-color: var(--color-green-2);
    border-color: var(--color-green-2-dark);
  }

  100% {
    background-color: var(--color-green-1);
    border-color: var(--color-green-1-dark);
  }
}

.main-content-search-form-button {
  -webkit-animation: mainContentSearchFormButton 2s linear infinite;
          animation: mainContentSearchFormButton 2s linear infinite;
  border: 0;
  border-bottom: 6px solid;
  color: var(--color-white);
  cursor: pointer;
  font-family: 'Roboto', sans-serif;
  font-size: 1.8rem;
  font-weight: 700;
  padding: 1rem;
  text-transform: uppercase;
}

.main-content-search-form-button:focus {
  opacity: .7;
  outline: none;
}
.main-content-search-form-button::-moz-focus-inner {
  border: 0;
}

.main-content-search-form-button:active {
  border-bottom: 0;
  border-top: 6px solid;
  opacity: 1;
}
.main-content-section {
  box-shadow: 3px 3px 2px rgba(0, 0, 0, 0.03);
}

.main-content-section--no-shadow {
  box-shadow: unset;
}

.main-content-section:not(:last-child) {
  margin-bottom: var(--size-4);
}
.main-content-search-form {
  display: -webkit-flex;
  display: flex;
}
.main-content-search-recent {
  -webkit-align-items: center;
          align-items: center;
  background: var(--color-grey-2);
  border-radius: 8px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.main-content-search-recent__left {
  -webkit-align-items: center;
          align-items: center;
  background: var(--color-grey-2);
  border-radius: 8px;
  display: -webkit-flex;
  display: flex;
}

.main-content-search-recent__title {
  color: var(--color-grey-3);
  font-size: 1.6rem;
  font-weight: 700;
  font-family: 'Ubuntu', sans-serif;
  padding: var(--size-2) var(--size-4);
  text-align: center;
  text-transform: uppercase;
}

.main-content-search-recent__items {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  margin-bottom: var(--size-2);
}

.main-content-search-recent__item {
  border: 0;
  border-radius: 20px;
  box-shadow: 3px 3px 2px rgba(0, 0, 0, 0.03);
  color: var(--color-white);
  cursor: pointer;
  font-size: 1.4rem;
  margin-top: var(--size-2);
  min-width: 5rem;
  padding: var(--size-1) var(--size-3);
  text-align: center;
}

.main-content-search-recent__item:nth-child(odd) {
  background-color: var(--color-blue);
}

.main-content-search-recent__item:nth-child(even) {
  background-color: var(--color-blue-dark);
}

.main-content-search-recent__item:not(:last-child) {
  margin-right: var(--size-2);
}

.main-content-search-recent__item:focus {
  background-color: var(--color-blue-light);
  outline: none;
}

.main-content-search-recent__item::-moz-focus-inner {
  border: 0;
}

.main-content-search-recent__right {
  padding-right: var(--size-2);
}

.main-content-search-recent-clear {
  background-color: transparent;
  border: 0;
  color: var(--color-grey-3);
  cursor: pointer;
  padding: var(--size-1) var(--size-2);
  -webkit-transition: .2s color ease;
  transition: .2s color ease;
}

.main-content-search-recent-clear:hover,
.main-content-search-recent-clear:focus {
  color: var(--color-grey-4);
  outline: none;
}

.main-content-search-recent-clear__icon {
  cursor: pointer;
  fill: currentColor;
  height: 2rem;
  position: relative;
  top: 2px;
  width: 2rem;
}
.main-content-info-box {
  background: var(--color-white);
}

.main-content-info-box > h2 {
  border-left: 5px solid;
  font-size: 1.8rem;
  font-weight: 700;
  padding: var(--size-2) var(--size-3);
}

.main-content-info-box > p {
  background: rgba(0, 0, 0, 0.05);
  color: var(--color-grey-4);
  line-height: 1.5;
  padding: var(--size-4);
}

/* Trivia */
.main-content-info-box--trivia {
  border-left-color: var(--color-trivia);
}

.main-content-info-box--trivia > h2 {
  color: var(--color-trivia);
}

/* Year */
.main-content-info-box--year {
  border-left-color: var(--color-year);
}

.main-content-info-box--year > h2 {
  color: var(--color-year);
}

/* Math */
.main-content-info-box--math {
  border-left-color: var(--color-math);
}

.main-content-info-box--math > h2 {
  color: var(--color-math);
}

.main-content-message {
  font-size: 1.6rem;
  text-align: center;
}

.main-content-message--error {
  color: var(--color-red);
}

.main-content-message--not-found {
  color: var(--color-grey-4);
}
@-webkit-keyframes loadingSpinner {
  0% {
    fill: var(--color-trivia);
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  50% {
    fill: var(--color-year);
  }

  100% {
    fill: var(--color-math);
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes loadingSpinner {
  0% {
    fill: var(--color-trivia);
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  50% {
    fill: var(--color-year);
  }

  100% {
    fill: var(--color-math);
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.spinner-icon {
  -webkit-animation-name: loadingSpinner;
          animation-name: loadingSpinner;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-duration: .8s;
          animation-duration: .8s;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  height: 3rem;
  width: 3rem;
}
.main-content-loading {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  width: 100%;
}
.main-content-center-wrapper {
  margin-top: 2rem;
  text-align: center;
}
.app {
  margin: var(--app-margin-size) auto;
  width: 50%;
}

@media (max-width: 780px) {
  .app {
    width: 90%;
  }
}
